//this is router config
import Vue from "vue";
import VueRouter from "vue-router";
Vue.use(VueRouter);
var routesconfig = [{
		path: '/',
		redirect: '/screen'
},
	{
		path: '/screen',
		component: resolve => require(['../../components/pages/screen/screen.vue'], resolve),
		children: [{
				path: '/',
				redirect: '/newsCenter'
			},
			{
				path: '/chuangke',
				component: resolve => require(['../../components/pages/screen/chuangke/chuangke.vue'], resolve)
			},
			{
				path: '/newsCenter',
				component: resolve => require(['../../components/pages/screen/newsCenter/newsCenter.vue'], resolve)
			},
			{
				path: '/review',
				component: resolve => require(['../../components/pages/screen/review/review.vue'], resolve)
			},
			{
				path: '/survey',
				component: resolve => require(['../../components/pages/screen/survey/survey.vue'], resolve)
			},
			{
				path: '/tjcyzc',
				component: resolve => require(['../../components/pages/screen/tjcyzc/tjcyzc.vue'], resolve)
			},
			{
				path: '/video',
				component: resolve => require(['../../components/pages/screen/video/video.vue'], resolve)
			},
		]
	},
	{
		path: '*',
		component: resolve => require(['../../components/pages/notfound/notfound.vue'], resolve)
	}

];

var router = new VueRouter({
	//mode: 'history',
	routes: routesconfig
});

export default router;


